import clsx from "clsx";
import { useState, useEffect, useCallback } from "react";
import { ChevronUp, ChevronDown, Trash2 } from "lucide-react";
import Papa from "papaparse";

export interface Transaction {
    id?: number;
    date: string;
    transaction_code?: string;
    name: string;
    memo?: string;
    category?: string;
    amount: number;
    status: "pending" | "confirmed";
}

type SortField = "date" | "name" | "amount" | "category" | "memo" | "status";
type SortDirection = "asc" | "desc";

interface TransactionTableProps {
    transactions: Transaction[];
    onUpdate: (index: number, field: keyof Transaction, value: any) => void;
    onDelete: (index: number) => void;
    onPasteImport: (data: any[]) => void;
    onSave?: () => void;
}

export function TransactionTable({ transactions, onUpdate, onDelete, onPasteImport, onSave }: TransactionTableProps) {
    const [sortField, setSortField] = useState<SortField>("date");
    const [sortDirection, setSortDirection] = useState<SortDirection>("desc");
    const [deleteConfirmIndex, setDeleteConfirmIndex] = useState<number | null>(null);

    // Sorted transactions
    const sortedTransactions = [...transactions].sort((a, b) => {
        let aVal = a[sortField] ?? "";
        let bVal = b[sortField] ?? "";

        if (sortField === "amount") {
            return sortDirection === "asc" ? a.amount - b.amount : b.amount - a.amount;
        }

        const comparison = String(aVal).localeCompare(String(bVal));
        return sortDirection === "asc" ? comparison : -comparison;
    });

    // Get original index from sorted position
    const getOriginalIndex = (sortedIndex: number): number => {
        const tx = sortedTransactions[sortedIndex];
        return transactions.findIndex(t => t === tx);
    };

    // Handle column header click for sorting
    const handleSort = (field: SortField) => {
        if (sortField === field) {
            setSortDirection(sortDirection === "asc" ? "desc" : "asc");
        } else {
            setSortField(field);
            setSortDirection("asc");
        }
    };

    // Sort indicator component
    const SortIndicator = ({ field }: { field: SortField }) => (
        <span className="ml-1 inline-flex flex-col text-[8px] leading-none opacity-60">
            <ChevronUp className={clsx("w-3 h-3 -mb-1", sortField === field && sortDirection === "asc" && "text-white opacity-100")} />
            <ChevronDown className={clsx("w-3 h-3", sortField === field && sortDirection === "desc" && "text-white opacity-100")} />
        </span>
    );

    // Global paste listener for CSV data
    useEffect(() => {
        const handlePaste = async (e: ClipboardEvent) => {
            // Don't intercept if user is typing in an input
            if ((e.target as HTMLElement)?.tagName === "INPUT" || (e.target as HTMLElement)?.tagName === "TEXTAREA") {
                return;
            }

            const clipboardData = e.clipboardData?.getData("text");
            if (!clipboardData) return;

            // Try to parse as CSV (Tab-separated for Excel / comma for CSV)
            const hasTabsOrCommas = clipboardData.includes("\t") || clipboardData.includes(",");
            const hasNewlines = clipboardData.includes("\n");

            if (hasTabsOrCommas && hasNewlines) {
                e.preventDefault();

                Papa.parse(clipboardData, {
                    header: true,
                    skipEmptyLines: true,
                    complete: (results) => {
                        if (results.data.length > 0) {
                            onPasteImport(results.data as any[]);
                        }
                    }
                });
            }
        };

        document.addEventListener("paste", handlePaste);
        return () => document.removeEventListener("paste", handlePaste);
    }, [onPasteImport]);

    // Handle delete confirmation
    const handleDeleteClick = (sortedIndex: number) => {
        setDeleteConfirmIndex(sortedIndex);
    };

    const confirmDelete = () => {
        if (deleteConfirmIndex !== null) {
            onDelete(getOriginalIndex(deleteConfirmIndex));
            setDeleteConfirmIndex(null);
        }
    };

    const cancelDelete = () => {
        setDeleteConfirmIndex(null);
    };

    return (
        <div className="bg-white border border-gray-300 shadow-sm rounded-sm overflow-hidden">
            <div className="flex justify-between items-center p-4 bg-gray-50 border-b border-gray-300">
                <h2 className="text-sm font-bold text-gray-700 uppercase tracking-wide">Transactions</h2>
                {onSave && (
                    <button
                        onClick={onSave}
                        className="bg-slate-800 text-white text-sm px-4 py-1.5 rounded hover:bg-slate-900 transition font-medium"
                    >
                        Save Changes
                    </button>
                )}
            </div>

            {/* Delete Confirmation Modal */}
            {deleteConfirmIndex !== null && (
                <div className="fixed inset-0 bg-black/30 flex items-center justify-center z-50" onClick={cancelDelete}>
                    <div className="bg-white rounded-lg shadow-xl p-6 max-w-sm mx-4" onClick={e => e.stopPropagation()}>
                        <h3 className="text-lg font-semibold text-gray-900 mb-2">Delete Transaction?</h3>
                        <p className="text-sm text-gray-600 mb-4">
                            Are you sure you want to delete "{sortedTransactions[deleteConfirmIndex]?.name}"? This action cannot be undone.
                        </p>
                        <div className="flex gap-3 justify-end">
                            <button
                                onClick={cancelDelete}
                                className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded hover:bg-gray-200 transition"
                            >
                                Cancel
                            </button>
                            <button
                                onClick={confirmDelete}
                                className="px-4 py-2 text-sm font-medium text-white bg-red-600 rounded hover:bg-red-700 transition"
                            >
                                Delete
                            </button>
                        </div>
                    </div>
                </div>
            )}

            <div className="overflow-x-auto">
                <table className="min-w-full border-collapse">
                    <thead className="bg-[#1e293b] text-white">
                        <tr>
                            <th
                                onClick={() => handleSort("date")}
                                className="px-4 py-3 text-left text-xs font-semibold uppercase tracking-wider border-r border-slate-700 cursor-pointer hover:bg-slate-700 transition select-none"
                            >
                                <span className="flex items-center">Date <SortIndicator field="date" /></span>
                            </th>
                            <th
                                onClick={() => handleSort("name")}
                                className="px-4 py-3 text-left text-xs font-semibold uppercase tracking-wider border-r border-slate-700 w-1/4 cursor-pointer hover:bg-slate-700 transition select-none"
                            >
                                <span className="flex items-center">Name <SortIndicator field="name" /></span>
                            </th>
                            <th
                                onClick={() => handleSort("amount")}
                                className="px-4 py-3 text-xs font-semibold uppercase tracking-wider border-r border-slate-700 text-right cursor-pointer hover:bg-slate-700 transition select-none"
                            >
                                <span className="flex items-center justify-end">Amount <SortIndicator field="amount" /></span>
                            </th>
                            <th
                                onClick={() => handleSort("category")}
                                className="px-4 py-3 text-left text-xs font-semibold uppercase tracking-wider border-r border-slate-700 cursor-pointer hover:bg-slate-700 transition select-none"
                            >
                                <span className="flex items-center">Category <SortIndicator field="category" /></span>
                            </th>
                            <th
                                onClick={() => handleSort("memo")}
                                className="px-4 py-3 text-left text-xs font-semibold uppercase tracking-wider border-r border-slate-700 cursor-pointer hover:bg-slate-700 transition select-none"
                            >
                                <span className="flex items-center">Memo <SortIndicator field="memo" /></span>
                            </th>
                            <th
                                onClick={() => handleSort("status")}
                                className="px-4 py-3 text-left text-xs font-semibold uppercase tracking-wider cursor-pointer hover:bg-slate-700 transition select-none"
                            >
                                <span className="flex items-center">Status <SortIndicator field="status" /></span>
                            </th>
                            <th className="w-10"></th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200">
                        {sortedTransactions.map((tx, sortedIdx) => {
                            const originalIdx = getOriginalIndex(sortedIdx);
                            return (
                                <tr key={originalIdx} className={clsx("hover:bg-blue-50/50 transition-colors group", tx.status === "pending" && "bg-yellow-50/30")}>
                                    {/* Date - Read Only */}
                                    <td className="px-4 py-3 text-sm text-gray-900 border-r border-gray-200">
                                        {tx.date}
                                    </td>
                                    {/* Name - Read Only */}
                                    <td className="px-4 py-3 text-sm font-medium text-gray-900 border-r border-gray-200">
                                        {tx.name}
                                    </td>
                                    {/* Amount - Read Only */}
                                    <td className={clsx(
                                        "px-4 py-3 text-sm text-right font-mono border-r border-gray-200",
                                        tx.amount < 0 ? "text-red-600" : "text-emerald-700 font-medium"
                                    )}>
                                        {tx.amount < 0 ? "-" : ""}${Math.abs(tx.amount).toFixed(2)}
                                    </td>
                                    {/* Category - Editable */}
                                    <td className="px-0 py-0 border-r border-gray-200">
                                        <input
                                            className="w-full bg-transparent p-3 focus:outline-none focus:bg-blue-50 focus:ring-2 focus:ring-blue-500 text-sm text-gray-600 border-transparent hover:bg-gray-50 transition"
                                            value={tx.category || ""}
                                            placeholder="Uncategorized"
                                            onChange={(e) => onUpdate(originalIdx, "category", e.target.value)}
                                        />
                                    </td>
                                    {/* Memo - Read Only */}
                                    <td className="px-4 py-3 text-sm text-gray-500 border-r border-gray-200 truncate max-w-[200px]">
                                        {tx.memo || "—"}
                                    </td>
                                    {/* Status - Read Only */}
                                    <td className="px-4 py-2 text-center">
                                        {tx.status === "pending" ? (
                                            <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-amber-100 text-amber-800">
                                                Pending
                                            </span>
                                        ) : (
                                            <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-emerald-100 text-emerald-800">
                                                Confirmed
                                            </span>
                                        )}
                                    </td>
                                    {/* Delete Button - Subtle */}
                                    <td className="px-2 py-2">
                                        <button
                                            onClick={() => handleDeleteClick(sortedIdx)}
                                            className="opacity-0 group-hover:opacity-100 p-1.5 text-gray-400 hover:text-red-500 hover:bg-red-50 rounded transition-all"
                                            title="Delete transaction"
                                        >
                                            <Trash2 className="w-4 h-4" />
                                        </button>
                                    </td>
                                </tr>
                            );
                        })}
                    </tbody>
                </table>
                {transactions.length === 0 && (
                    <div className="text-center p-12 text-gray-400 bg-gray-50/50">
                        No transactions found. Import a CSV to get started.
                    </div>
                )}
            </div>
        </div>
    );
}
